/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import Dropdown from "./components/UI/Dropdown";
import PostRes from "./components/PostRes";
import {
  대,
  의류_중,
  식품_중,
  계절List,
  월List,
  요일List,
  시간대List,
} from "./assets/temp";
import { Suspense, useEffect, useState } from "react";
import { TextField } from "@mui/material";
import Swal from "sweetalert2";

const container = css`
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  .inputBox {
    width: 50%;
    height: 50%;
    border: 1px solid black;
    display: flex;
    .input {
      flex-direction: column;
      margin-left: 20px;
      margin-top: 20px;
    }
    .result {
      width: 100%;
      height: 100%;
    }
  }
`;

function App() {
  const [중분류List, set중분류List] = useState(null);

  const [대분류, set대분류] = useState(null);
  const [중분류, set중분류] = useState(null);
  const [계절, set계절] = useState(null);
  const [월, set월] = useState(null);
  const [요일, set요일] = useState(null);
  const [시간대, set시간대] = useState(null);
  const [시간, set시간] = useState(null);

  useEffect(() => {
    if (대분류 == "의류") {
      set중분류List(() => 의류_중);
    } else if (대분류 == "식품") {
      set중분류List(() => 식품_중);
    }
  }, [대분류]);

  const regex = /^(0|[1-9]|1\d|2[0-4])$/;

  const handleChange = (e) => {
    if (regex.test(e.target.value)) {
      set시간(() => e.target.value);
    } else {
      Swal.fire({
        title: "Error!",
        text: "0 ~ 24사이의 숫자를 입력해 주세요",
        icon: "error",
        confirmButtonText: "닫기",
      });
      set시간(() => "");
    }
  };
  return (
    <div className="App">
      <div css={container}>
        <div className="inputBox">
          <div className="input">
            <div className="대분류">
              <Dropdown data={대} label={"대분류"} handleData={set대분류} />
            </div>
            <div className="중분류">
              <Dropdown
                data={중분류List}
                label={"중분류"}
                handleData={set중분류}
              />
            </div>
            <div className="계절">
              <Dropdown data={계절List} label={"계절"} handleData={set계절} />
            </div>
            <div className="월">
              <Dropdown data={월List} label={"월"} handleData={set월} />
            </div>
            <div className="요일">
              <Dropdown data={요일List} label={"요일"} handleData={set요일} />
            </div>
            <div className="시간대">
              <Dropdown
                data={시간대List}
                label={"시간대"}
                handleData={set시간대}
              />
            </div>
            <div className="시간">
              <TextField
                id="standard-basic"
                label="시간"
                variant="standard"
                onChange={handleChange}
                sx={{ marginLeft: "10px" }}
              />
            </div>
          </div>
          <div className="result">
            <Suspense fallback={<h1>loading...</h1>}>
              <PostRes
                대분류={대분류}
                중분류={중분류}
                요일={요일}
                월={월}
                계절={계절}
                시간={시간}
                시간대={시간대}
              />
            </Suspense>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
