/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import { useState } from "react";
import Swal from "sweetalert2";
import axios from "axios";
import { Button } from "@mui/material";
import { useMutation } from "react-query";

const container = css`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .res {
    margin-bottom: 20px;
    font-weight: bold;
  }
  .btnSection {
  }
`;

export default function PostRes({
  대분류,
  중분류,
  요일,
  월,
  계절,
  시간,
  시간대,
}) {
  const [결과, set결과] = useState(null);

  const data = {
    category1: 대분류,
    category2: 중분류,
    day: 요일,
    month: 월,
    season: 계절,
    time: 시간,
    timezone: 시간대,
  };

  const postRes = useMutation(
    () =>
      axios({
        method: "post",
        url: "http://127.0.0.1:8000/",
        data,
      }),
    {
      onSuccess: (res) => set결과(() => res.data),
      onError: (err) => console.log(err),
      suspense: true,
    }
  );
  const handleClick = () => {
    if ((대분류, 중분류, 요일, 월, 계절, 시간, 시간대)) {
      console.log(data);
      postRes.mutate();
    } else {
      Swal.fire({
        title: "Error!",
        text: "모든 항목을 채워주세요",
        icon: "error",
        confirmButtonText: "닫기",
      });
    }
  };
  return (
    <div css={container}>
      <div className="res">{postRes.isLoading ? "loading..." : 결과}</div>
      <div className="btnSection">
        <Button
          variant="contained"
          className="btn"
          onClick={handleClick}
          css={{ width: "100px", height: "50px" }}>
          click
        </Button>
      </div>
    </div>
  );
}
